$SFPro: 'SF Pro Display', sans-serif;
$transition: .3s ease-in-out;

:root {
  --black: #06253B;
  --white: #ffffff;
  --light-gray: #F0F1F2;
  --gray: #969696;
  --yellow: #EABD37;
}

html {
  background: var(--yellow);
}

body {
  padding: 19px 0;
  position: relative;
  min-width: 320px;
  color: var(--black);
  font-family: $SFPro;

}

.overflow-wrap {
  overflow: hidden;
}

* {
  outline: none;
  word-wrap: break-word;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

a, a:focus, a:hover {
  text-decoration: none;
}

ol, ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

img {
  border: 0;
  max-width: 100%;
  vertical-align: middle;
}

button {
  padding: 0;
  cursor: pointer;
}

input,
button,
textarea {
  border-radius: 0;
  -webkit-appearance: none;
}

select {
  -webkit-appearance: none;
}

.main-btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 15px;
  width: 100%;
  min-height: 50px;
  font-weight: 500;
  font-size: 17px;
  line-height: 1;
  letter-spacing: -0.41px;
  border-radius: 30px;
  background: var(--white);
  border: 1px solid var(--light-gray);
  transition: color $transition, border-color $transition, background-color $transition;
  &:hover {
    color: var(--yellow);
  }
}

.info-section {
  padding: 17px 15px;
  margin-top: 7px;
  position: relative;
  z-index: 1;
  background: var(--white);
  border-radius: 0 0 10px 10px;

  &:before {
    content: '';
    height: 14px;
    background: url(../images/line.svg) left bottom repeat-x;
    position: absolute;
    top: -7px;
    left: 0;
    right: 0;
    z-index: -1;
  }

  .title {
    display: flex;
    align-items: center;
    flex-direction: column;

    span.icon-info {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 40px;
      height: 40px;
      margin-bottom: 11px;
      padding: 5px;
      font-size: 25px;
      color: var(--yellow);
      border-radius: 50%;
      box-shadow: -4px -4px 8px 2px rgba(0, 0, 0, 0.04), 0px 1.01655px 4.425px rgba(13, 108, 237, 0.0383468);
    }

    h1 {
      margin: 0 0 15px 0;
      font-weight: normal;
      font-size: 22px;
      line-height: 1.5;
      text-align: center;
    }
  }

  .info-list {
    border-radius: 10px;
    box-shadow: -4px -4px 8px 2px rgba(0, 0, 0, 0.04), 4px 4px 8px 2px rgba(0, 0, 0, 0.04);

    ul {
      li {
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        padding: 13px 24px;
        border-bottom: 1px solid #F0F1F2;

        &:last-child {
          border-color: transparent;
        }

        .icon {
          display: flex;
          align-items: center;
          justify-content: center;
          flex-shrink: 0;
          width: 40px;
          height: 40px;
          padding: 5px;
          margin: 4px 0;
          font-size: 25px;
          color: var(--yellow);
          border-radius: 50%;
          box-shadow: -4px -4px 8px 2px rgba(0, 0, 0, 0.04), 0px 1.01655px 4.425px rgba(13, 108, 237, 0.0383468);
        }

        .data {
          width: calc(100% - 40px);
          padding: 0 0 0 19px;

          h2 {
            margin: 0;
            font-weight: 500;
            font-size: 15px;
            line-height: 1.33;
            color: var(--gray);
          }

          p {
            margin: 0;
            font-weight: bold;
            font-size: 24px;
            line-height: 1.25;
            letter-spacing: 0.35px;
          }
        }

        .location {
          width: 100%;
          padding: 7px 0 0 0;

          p {
            margin: 0;
            font-weight: bold;
            font-size: 18px;
            line-height: 1.56;
            letter-spacing: 0.35px;
            a {
              color: inherit;
              transition: color $transition;
              &:hover {
                color: var(--yellow);
              }
            }
          }
        }
      }
    }

    .btn-wrap {
      padding: 2px 24px 17px 24px;
    }
  }

  .logo-wrap {
    padding: 17px 20px 0 20px;
    text-align: center;

    a {
      display: inline-block;
    }
  }
}

.add-btn {
  padding-top: 24px;
}